header {
  background-color: tomato;
  font-family: sans-serif;
}

/* contactDesign */

.contact-wrapper {
  background-color: #f9e11e;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 50px;
  font-size: 1.5rem;
}

.contact-wrapper .contact {
  display: flex;
  gap: 5rem;
}

.contact-wrapper .contact .contact-icons {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.contact-wrapper .contact .contact-icons .icons {
  font-size: 1.8rem;
}

.contact-wrapper .contact .contact-icons button {
  background-color: rgb(233, 94, 24);
  outline: none;
  border: none;
  border-radius: 20px;
  padding: 0.8rem 4rem;
  color: white;
  font-size: 1.4rem;
  font-weight: 600;
}

/* nav-bar-design */

.nav-bar {
  width: 95%;
  margin: auto;
  display: flex;
  align-items: center;
}

/* .left-nav-section */

.nav-bar .left-nav-section {
  flex: 1;
}
.nav-bar .left-nav-section img {
  width: 65px;
  height: 65px;
}

/* .middle-nav-section */
.nav-bar .middle-nav-section {
  flex: 3;
}

.nav-bar .middle-nav-section .nav-bar-search {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  font-size: 1.6rem;
  font-weight: 700;
}

.nav-bar .middle-nav-section .nav-bar-search input {
  width: 70%;
  outline: none;
  border: none;
  height: 35px;
  padding: 0rem 1rem;
  border-radius: 5px 0 0 5px;
}
.nav-bar .middle-nav-section .nav-bar-search .search {
  background-color: #89bad0;
  height: 35px;
  padding: 0 0.6rem;
  font-size: 2.6rem;
  display: flex;
  align-items: center;
}
.nav-bar .right-nav-section-mobile {
  background-color: green;
  display: none;
  align-items: center;
  justify-content: space-evenly;
  font-size: 2rem;
  right: 0%;
  top: 0%;
  position: absolute;
  width: 100%;
  z-index: 99999;
  height: 100vh;
  z-index: 99999;
}

