/* right-section-design */

.nav-bar .right-nav-section {
  flex: 1;
}

.nav-bar .right-nav-section .nav-icons {
  display: flex;
  font-size: 1.7rem;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  cursor: pointer;
}
.nav-bar .right-nav-section .nav-icons .icons {
  font-size: 2.5rem;
}
.nav-bar .right-nav-section .nav-icons .account .sub-menu-account {
  display: none;
}

.nav-bar .right-nav-section .nav-icons .account:hover > .sub-menu-account {
  display: block;
  position: absolute;
  border-radius: 5px;
  top: 12.5%;
  right: 1%;
  width: 10%;
  background-color: rgb(219, 213, 213);
  z-index: 9999;
  padding: 1rem;
}
.nav-bar .menu {
  display: none;
}

@media only screen and (max-width: 1040px) {
  .contact-wrapper {
    background-color: #f9e11e;
    display: flex;
    flex-direction: column;
    font-size: 1.3rem;
    height: 80px;
  }
}

@media only screen and (max-width: 768px) {
  .nav-bar .left-nav-section img {
    width: 45px;
    height: 45px;
  }
  .nav-bar .right-nav-section {
    display: none;
  }
  .contact-wrapper {
    background-color: #f9e11e;
    display: flex;
    gap: 0;
    flex-direction: column;
    font-size: 1.3rem;
    height: 180px;
  }
  .contact-wrapper .contact {
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
  }
  .contact-wrapper .contact .contact-icons {
    display: flex;
    align-items: center;
  }

  .contact-wrapper .contact .contact-icons button {
    background-color: rgb(233, 94, 24);
    outline: none;
    border: none;
    border-radius: 20px;
    /* padding: 0.8rem 4rem; */
    color: white;
    font-size: 1rem;
    font-weight: 600;
  }
  .nav-bar {
    width: 95%;
    margin: auto;
    display: flex;
    /* flex-direction: column; */
    align-items: center;
  }
  .nav-bar .left-nav-section {
    flex: 0;
  }
  .nav-bar .left-nav-section .middle-nav-section {
    flex: 2;
  }
  .nav-bar .left-nav-section .right-nav-section {
    flex: 1;
    display: flex;
  }
  .nav-bar .menu {
    display: block;
    font-size: 2.5rem;
  }
}
